.header {
    height: 65px;
    background-color: var(--pozadina2);
    font-size: medium;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "inter";
    position: fixed;
    width: 100%;
}

.cityInput {
    font-size: medium;
    border: none;
    border-radius: 5px;
    padding: 8px;
    font-family: "inter";
}
.cityInput:focus {
    outline: none;
}
